export const emailValidate = [
  value => {
    const pattern = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return pattern.test(value) || "   .  johndove@mail.com";
  }
];

export const textValidate = [
  v => (v && v.length >= 3) || "   3"
];

export const vrequired = [
  v => !!v || "  '  "
];
